<template>
	<section>		
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline='true' >
				<el-form-item >
					<el-select v-model="filters.status" placeholder="请选择订单类型">
						<el-option label="全部" value=" "></el-option>
						<el-option label="已下单" value="1"></el-option>
						<el-option label="已发货" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item >
					<el-input v-model="filters.order_number" placeholder="请输入订单号"></el-input>
				</el-form-item>	
				<el-form-item style="float: right;">
					<el-button type="primary" @click='searchList()' > 查询 </el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-table :data="info" border stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column prop="tablenum" label="#" width="60">
			</el-table-column>
			<el-table-column prop="statusName" label="订单状态" width="100">
			</el-table-column>
			<el-table-column prop="consigneeName" label="姓名" width="100">
			</el-table-column>
			<el-table-column prop="consigneePhone" label="手机号" width="120">
			</el-table-column>
			<el-table-column prop="prizeType" label="奖品类型" width="100">
			</el-table-column>
			<el-table-column prop="ordersNumber" label="订单号" >
			</el-table-column>
			<el-table-column prop="prizeName" label="商品名称" >
			</el-table-column>
			<el-table-column prop="consigneeAddress" label="地址" >
			</el-table-column>
			<!-- <el-table-column prop="customerRemark" label="备注" >
			</el-table-column> -->
			<el-table-column label="操作" width="90">
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)" v-if="scope.row.status==1">发货</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" @current-change="handleCurrentChange" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		<!--修改信息-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px"  ref="editForm" :rules="rules">				
				<el-form-item label="订单号" prop="ordersNumber">
					<el-input  v-model="editForm.ordersNumber"   auto-complete="off" ></el-input>
				</el-form-item>
				<el-form-item label="物流名称" prop="logisticsCompanyName" >
					<el-input  v-model="editForm.logisticsCompanyName" ></el-input>
				</el-form-item>
				<el-form-item label="物流单号" prop="logisticsOrdersNumber" >
					<el-input  v-model="editForm.logisticsOrdersNumber"></el-input>
				</el-form-item>
				<el-form-item label="客服备注" prop="remark" >
					<el-input type="textarea" v-model="editForm.remark" ></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import qs from 'qs';
	import md5 from 'js-md5';
	export default {
		data(){
			return {			
				info:[],
				listLoading:false,
				sels:[],
				total:0,
				page:1,
				pagesize:10,
				editFormVisible:false,
				editLoading:false,
				filters:{
					status:'',
					order_number:''
				},
				rules: {
		          logisticsCompanyName: [
		            { required: true, message: '请输入快递公司', trigger: 'blur' },
		          ],
		          logisticsOrdersNumber: [
		            { required: true, message: '请输入快递单号', trigger: 'blur' },
		          ],
		          remark: [
		            { required: true, message: '请输入客服备注', trigger: 'change' },
		          ]
		        },		        
				editForm:{
					ordersNumber:'',
					logisticsCompanyName:'',
					logisticsOrdersNumber:'',
					remark:''
				},
			}
		},
		mounted(){
			this.getTableData();
		},
		methods:{
			searchList(){
				this.getTableData();
			},
			getTableData(){
	        		let sendData= {
	        			number: this.pagesize,
	        			page:this.page,
						order_number:this.filters.order_number,
						status:this.filters.status					
	        		};
	        		let self = this;
	        		this.$http.get('v2/back/orders/list',{
	        			headers: {'Accept': 'application/json'},
	        			params:sendData
	        		})
	        		.then(function(res){
	        			self.listLoading = false;
	        			if(res.data.code==200){
	        				if(res.data.data!=null&&res.data.data!=''){
	        					let data = res.data.data;
			        			for(var i=0;i<data.length;i++){
			        				let j =i+1;
			        				data[i].tablenum = j;
									if(data[i].status==1){
										data[i].statusName = '已下单'
									}else{
										data[i].statusName = '已发货'
									}
			        			}
								self.info = data;
								self.total = res.data.count;
	        				}else{
	        					self.info=[];
	        				}
	        			}else if(res.data.code==401){
	        				self.$message.error(res.data.message);
	        				setTimeout(function(){
	        					self.$router.push({ path: '/login' });
	        				},2000)
	        			}else{
	        				self.$message.error(res.data.message);
	        			}
	        		})
	        		.catch(function(res){
	        			self.listLoading = false;
	        			self.$message.error(res.data.message);
	        		})	
			},
			editSubmit(){
				let self = this;
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);	
							console.log(para)
							let sendData = {
								ordersNumber:para.ordersNumber,
								logisticsCompanyName:para.logisticsCompanyName,
								logisticsOrdersNumber:para.logisticsOrdersNumber,
								remark:para.remark
							};
							this.$http({
								method:'post',
								url:'/v2/back/orders/delivery',
								data:qs.stringify(sendData),
								headers:{
									'Accept': 'application/json',
								},
							})
							.then((res) => {
								//console.log(res,para)
								this.editFormVisible = false;
								this.editLoading = false;
								if(res.data.code==200){
									//NProgress.done();
									this.$message({
										message: res.data.message,
										type: 'success'
									});
									// this.$refs['editForm'].resetFields();
									this.getTableData();
								}else{
									this.$message({
										message: res.data.message,
										type: 'error'
									});
								}
							});
						});
					}
				});
			},									
			handleEdit(index,row){	
				// console.log(row)
				this.editFormVisible = true;			
				this.editForm = Object.assign({},row);
			},
			selsChange(sels){
				this.sels = sels;
			},
			handleCurrentChange(val){
				this.page = val;
				this.getTableData()
			},
			format(time,format){
				var t = new Date(time);
				var tf = function(i){return (i < 10 ? '0' : '') + i};
				return format.replace(/yyyy|MM|dd|hh|mm|ss/g, function(a){
					switch(a){
						case 'yyyy':
							return tf(t.getFullYear());
							break;
						case 'MM':
							return tf(t.getMonth() + 1);
							break;
						case 'mm':
							return tf(t.getMinutes());
							break;
						case 'dd':
							return tf(t.getDate());
							break;	
						case 'hh':
							return tf(t.getHours());
							break;
						case 'mm':
							return tf(t.getMinutes());
							break;
						case 'ss':
							return tf(t.getSeconds());
							break;
						}
				})
			},
		}
	}
</script>	
<style scoped>
	section{
		font-size: 18px;
		font-weight: 600;
		.pwd{
			.el-form-item__label{
				width: 100px !important;
			}
			.el-form-item__content{
				margin-left: 100px !important;
			}
		}
	}
	div .el-select{
		width: 100% !important;
	}
</style>


